/* global Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    console.log("Taskpane cargado correctamente");

    const boton = document.getElementById("run");
    if (boton) {
      boton.onclick = pintarCelda;
    }
  }
});

async function pintarCelda() {
  try {
    await Excel.run(async (context) => {
      const hoja = context.workbook.worksheets.getActiveWorksheet();
      const celda = hoja.getRange("A1");
      celda.values = [["Â¡Hola desde el taskpane!"]];
      await context.sync();
    });
  } catch (error) {
    console.error("Error al pintar la celda:", error);
  }
}
